a, a:visited {
  color: #666;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

h1, h2, h3, h4 {
  font-family: "Roboto Condensed", "Helvetica Neue", "Helvetica";
  font-weight: 400;
  line-height: 1.4;
  color: #444;
  letter-spacing: -0.04em;
}
h1 small,
h1 .small, h2 small,
h2 .small, h3 small,
h3 .small, h4 small,
h4 .small {
  font-weight: normal;
  line-height: 1;
  color: #b3b3b3;
}
h1 a, h2 a, h3 a, h4 a {
  color: #444;
}

h1, h2, h3, h4 {
  margin-top: 20px;
  margin-bottom: 10px;
}
h1 small,
h1 .small, h2 small,
h2 .small, h3 small,
h3 .small, h4 small,
h4 .small {
  font-size: 65%;
}

h1 {
  font-size: 30px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 25px;
  position: relative;
  padding: 0 0 20px 0;
}
h1::after {
  content: "";
  position: absolute;
  width: 60px;
  height: 3px;
  background-color: #444;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
}

h2 {
  font-size: 26px;
  margin-bottom: 20px;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 20px;
}

body {
  background: #F8F8F8;
  font-family: "Roboto", "Helvetica Neue", "Helvetica";
  font-size: 15px;
  line-height: 27px;
  color: #666;
}

header#header {
  background: #1C252C;
  color: #FFD270;
  font-family: "Roboto Condensed", "Helvetica Neue", "Helvetica";
  font-weight: 700;
  letter-spacing: -0.04em;
  text-align: center;
  font-size: 70px;
  line-height: 60px;
  padding: 50px 30px;
  margin: 0 0 30px 0;
}
header#header h1 {
  color: #FFD270;
  font-size: 70px;
  line-height: 90px;
  font-weight: 700;
}
header#header h1::after {
  background: rgba(255, 210, 112, 0.75);
}
header#header h2 {
  color: rgba(255, 210, 112, 0.5);
}

footer#footer {
  background: #DDD;
  color: #888;
  padding: 30px 0;
  margin-top: 30px;
}
footer#footer a, footer#footer a:visited {
  color: #888;
}

label {
  font-weight: bold;
  display: block;
  margin-bottom: 5px;
}

p.help {
  color: #999;
}

#toggleDropify {
  font-size: 12px;
  text-transform: uppercase;
  background: #DDD;
  color: #888;
  font-weight: bold;
  border: 0;
  padding: 6px 10px;
  border-radius: 4px;
  margin-left: 10px;
  transition: background 0.1s linear;
}
#toggleDropify:hover {
  background: #EEE;
}

@media (max-width: 480px) {
  header#header {
    padding: 30px 15px;
  }
  header#header h1 {
    font-size: 44px;
    line-height: 70px;
    padding-bottom: 15px;
    margin-bottom: 15px;
  }
  header#header h2 {
    font-size: 20px;
  }
}